import VueRouter from "vue-router";

import Home from "../components/tab/Home.vue"
import Recommend from "../components/tab/Recommend.vue"
import Category from "../components/tab/Category"
import Mine from "../components/tab/Mine.vue"
import ProductDetail from "../components/pages/ProductDetail.vue"
// import ElectricityFee from "../components/HouseholdPay/ElectricityFee.vue"
// import WaterFee from "../components/HouseholdPay/WaterFee.vue"

var router=new VueRouter({
  routes: [
    { path: '/', redirect: '/home' },
    { path: '/home', component: Home },
    { path: '/recommend', component: Recommend },
    { path: '/category', component: Category },
    { path: '/mine', component: Mine  },
    { path: '/ProductDetail', component: ProductDetail  },
    // { path: '/electricityFee', component: ElectricityFee  },
    // { path: '/waterFee', component: WaterFee  },
  ],

  linkActiveClass: 'mui-active'
});

export default router



